.container {
  width: 100vw;
  overflow: hidden;
}

.carousel-container {
  width: 20.4rem;
  margin-left: -5rem;
  height: 7.9rem;
  line-height: 7.9rem;
}
.carousel-container img {
  width: 5.1rem;
  height: 6.9rem;
  display: inline-block;
  float: left;
  margin: 0.5rem 0.1rem;
  border-radius: 0.2rem;
}
.carousel-container .middle-img {
  width: 6.6rem;
  height: 7.9rem;
  margin-top: 0;
}
.carousel-container .css-move-small-left {
  width: 5.1rem;
  height: 6.9rem;
  margin: 0.5rem 0;
  -webkit-transform: translate(-5.2rem);
          transform: translate(-5.2rem);
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.carousel-container .css-move-left {
  -webkit-transform: translate(-2rem);
          transform: translate(-2rem);
  -webkit-transition: -webkit-transform 1s ease;
  transition: -webkit-transform 1s ease;
  transition: transform 1s ease;
  transition: transform 1s ease, -webkit-transform 1s ease;
}
.carousel-container .css-move-big-left {
  width: 6.6rem;
  height: 7.9rem;
  margin-top: 0;
  -webkit-transform: translate(-5.1rem);
          transform: translate(-5.1rem);
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.carousel-container .css-move-small-right {
  width: 5.1rem;
  height: 6.9rem;
  margin: 0.5rem 0;
  -webkit-transform: translate(5.4rem);
          transform: translate(5.4rem);
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.carousel-container .css-move-big-right {
  width: 6.6rem;
  height: 7.9rem;
  margin-top: 0;
  -webkit-transform: translate(5.3rem);
          transform: translate(5.3rem);
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.carousel-container .css-move-right {
  -webkit-transform: translate(2rem);
          transform: translate(2rem);
  -webkit-transition: -webkit-transform 1s ease;
  transition: -webkit-transform 1s ease;
  transition: transform 1s ease;
  transition: transform 1s ease, -webkit-transform 1s ease;
}
